import React from "react";

const CarouselArrow = ({
  currentActiveImage,
  slideChange,
  directionClass,
  direction,
  inactiveReason,
}) => {
  const onSlideChange = (e) => {
    slideChange(e);
  };

  return (
    <svg
      width="29px"
      height="50px"
      className={`carousel_switch ${directionClass} ${
        currentActiveImage === inactiveReason ? "inactive" : ""
      }`}
      onClick={onSlideChange}
      data-direction={direction}
      data-testid={direction}
    >
      <g transform="matrix(1 0 0 1 -46 -723 )">
        <path
          className="pointerEvents_block"
          d="M 28.5017182130584 3.00601202404809  C 28.8339060710195 3.34001336005343  29 3.72411489645958  29 4.15831663326653  C 29 4.59251837007348  28.8339060710195 4.97661990647962  28.5017182130584 5.31062124248497  L 8.9192439862543 25  L 28.5017182130584 44.689378757515  C 28.8339060710195 45.0233800935204  29 45.4074816299265  29 45.8416833667335  C 29 46.2758851035404  28.8339060710195 46.6599866399466  28.5017182130584 46.9939879759519  L 26.0103092783505 49.498997995992  C 25.6781214203895 49.8329993319973  25.2961053837343 50  24.8642611683849 50  C 24.4324169530355 50  24.0504009163803 49.8329993319973  23.7182130584192 49.498997995992  L 0.498281786941581 26.1523046092184  C 0.166093928980527 25.8183032732131  0 25.4342017368069  0 25  C 0 24.5657982631931  0.166093928980527 24.1816967267869  0.498281786941581 23.8476953907816  L 23.7182130584192 0.501002004008017  C 24.0504009163803 0.16700066800267  24.4324169530355 0  24.8642611683849 0  C 25.2961053837343 0  25.6781214203895 0.16700066800267  26.0103092783505 0.501002004008017  L 28.5017182130584 3.00601202404809  Z "
          fillRule="nonzero"
          fill="#e3e1e1"
          stroke="none"
          transform="matrix(1 0 0 1 46 723 )"
        />
      </g>
    </svg>
  );
};

export default CarouselArrow;
